.btn {
  width: 110px;
  height: 40px;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  outline: none;
  align-content: center;
  justify-content: center;
  align-items: center;
  display: flex;
}

.btn__icon-wrap {
  width: 19px;
  height: 17px;
  background-repeat: no-repeat;
  background-size: contain;
}

.btn --orange {
  color: var(--white);
  background-color: var(--auxiliary-orange);
}

.btn--white {
  color: var(--grapefruit);
  background-color: var(--white);
  border: 1px solid var(--grapefruit);
}

.gallery {
  flex-wrap: wrap;
  gap: 32px;
  display: flex;
}

.gallery__item {
  border: 1px solid var(--grapefruit);
  border-radius: 4px;
}

.gallery__link {
  margin-bottom: 16px;
  display: block;
}

.gallery__img {
  margin-bottom: 48px;
}

.gallery__subtitle {
  text-align: center;
  font-size: 26px;
  line-height: 1.62;
}

.gallery__btns {
  justify-content: space-between;
  gap: 16px;
