  display: flex;
}

.hero {
  margin-top: 60px;
}

.hero__overlay {
  height: 245px;
  max-width: 1600px;
  text-align: center;
  background-image: url("hero-bg-mob.785011f0.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  align-items: center;
  margin-bottom: 60px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  display: flex;
}

.hero__title {
  color: var(--grapefruit);
  font-family: Poppins, sans-serif;
  font-size: 22px;
  font-weight: 600;
  line-height: 1.9;
}

@media screen and (min-width: 1280px) {
  .hero__title {
    font-size: 44px;
    line-height: 1.36;
  }
}

.hero__text {
  letter-spacing: .02em;
  color: #000;
