  top: 0;
  left: 0;
}

.logo {
  width: 124px;
  margin-right: auto;
}

.container-header {
  display: flex;
}

.menu-btn {
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  padding: 0;
  display: flex;
}

@media screen and (min-width: 768px) {
  .menu-btn {
    display: none;
  }
}

.menu-btn__icon {
  fill: var(--black);
}

.menu-btn:active .menu-btn__icon {
  fill: var(--grapefruit);
}

body {
  position: relative;
}

.mob-menu {
  width: 320px;
  height: 100vh;
  background: var(--white);
  z-index: 1;
  padding: 20px;
  position: absolute;
  top: 0;
  right: 0;
  overflow: hidden;
}

.mob-menu.is-hidden {
  opacity: 0;
  transform: translate(50%, -60%)scale3d(0, 0, 1);
}

.mob-menu-form {
  letter-spacing: .02em;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

.mob-menu-form input {
  width: 280px;
  border: 1px solid #fd5103;
  border-radius: 4px;
  margin-top: 91px;
  padding: 8px;
}

.mob-menu-nav__item:active {
  background-color: var(--grapefruit);
}

.mob-menu-nav__link {
  color: var(--black);
  padding: 16px 0;
  font-size: 16px;
  line-height: 1.2;
  display: block;
}

.mob-menu__btn {
  width: 24px;
  height: 24px;
  cursor: pointer;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 4px;
  right: 4px;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

@media screen and (max-width: 767px) {
  .nav {
    display: none;
  }
}

.nav__list {
  flex-direction: row;
  align-items: center;
  gap: 28px;
  display: flex;
}

.nav__link {
  color: var(--black);
  line-height: 1.14;
}

.nav__link--current, .nav__link:hover, .nav__link:focus {
  color: var(--grapefruit);
  text-decoration: underline;
}

